
th a {
    margin-right: 8px !important;
}

.add-user {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 6px 6px 20px 4px #ececec;
    margin: 20px 200px;
    padding: 80px 0;
}

.add-user input {
    width: 800px;
    margin-bottom: 10px;
    outline: none;
}

.add-user button {
    width: 100%;
}

@media(max-width: 768px) {
    .add-user {
        box-shadow: 6px 6px 20px 4px #ececec;
        margin: 20px auto;
        padding: 80px 0;
    }
    
    .add-user input {
        width: auto;
        margin-bottom: 10px;
        outline: none;
    }    
}
